// {useState, useEffect} - хуки
import React, {useState, useEffect} from "react";
import axios from "axios";
import logo from './logo.svg';
import './App.css';

function App() {
  // people = []
  const [people, setPeople] = useState([])
  // Сходить за данныйми через axios
  useEffect(() => {
    axios({
      method: 'GET',
      url: 'http://127.0.0.1:8000/api/test-api'
    }).then(response => {  // получить ответ
      setPeople(response.data)  // присвоить ответ переменной people через 'setPeople'
    })
  }, [])  // Что бы небыло бесконечных запросов
  return (
    <div className="App">
      <h1>Hello from React to Django</h1>
      <ul>
        {/*[{'id': 1, 'name': 'John'}, {'id': 2, 'name': 'Jane'}]*/}
        {people.map(p => (
          <li key={p.id}>{p.name}</li>
        ))}
      </ul>
    </div>
  );
}

export default App;
